"use strict";
const TemplateStore_1 = require("../TemplateStore");
const TemplateStoreConfiguration_1 = require("../TemplateStoreConfiguration");
const FileStore_1 = require("../FileStore");
const Logger_1 = require("../Logger");
console.log("Listing...");
const config = new TemplateStoreConfiguration_1.TemplateStoreConfiguration();
const fileStore = new FileStore_1.FileStore();
const logger = new Logger_1.Logger();
const store = new TemplateStore_1.TemplateStore(config, fileStore, logger);
const command = new ListCommand(store);
command.execute();
class ListCommand {
    constructor(store) {
        this.store = store;
    }
    execute() {
        this.store.initialize()
            .then(this.listItems);
    }
    listItems() {
        console.log("listing...");
    }
}
exports.ListCommand = ListCommand;



