"use strict";
const chalk = require("chalk");
class Logger {
    log(message) {
        console.log(message);
    }
    warn(message) {
        console.log(chalk.yellow(message));
    }
    error(message) {
        console.log(chalk.red(message));
    }
}
exports.Logger = Logger;



